import { useState } from "react";
import FirstStep from "./FirstStep";
import SecondStep from "./SecondStep";
import TableStep from "./TableStep";
import ThirdStep from "./ThirdStep";
const AllSteps = () => {
  const [count, setCount] = useState(1);
  const [formData, setFormData] = useState({
    firstName: "",
    lastName: "",
    contact: "",
    dateOfBirth: "",
    address: "",
    university: "",
    degree: "",
    graduation: "",
    employer: "",
    designation: "",
    experience: "",
  });
  const [isUpdate, setIsUpdate] = useState(false);

  return (
    <div className="AllStep">
      {count === 1 && (
        <>
          <FirstStep
            count={count}
            setCount={setCount}
            formData={formData}
            setFormData={setFormData}
          />
          <TableStep
            setFormData={setFormData}
            formData={formData}
            isUpdate={isUpdate}
            setIsUpdate={setIsUpdate}
          />
        </>
      )}
      {count === 2 && (
        <SecondStep
          count={count}
          setCount={setCount}
          formData={formData}
          setFormData={setFormData}
        />
      )}
      {count === 3 && (
        <ThirdStep
          count={count}
          setCount={setCount}
          formData={formData}
          setFormData={setFormData}
          isUpdate={isUpdate}
          setIsUpdate={setIsUpdate}
        />
      )}
      {/* {count === 4 && (
        <>
          <ThirdStep
            count={count}
            setCount={setCount}
            formData={formData}
            setFormData={setFormData}
          />
         
        </>
      )} */}
    </div>
  );
};

export default AllSteps;
